import "./styles.css";

import type { ReactNode } from "react";

interface Props {
  show: boolean;
  background: string;
  color: string;
  callback: () => void;
  children: ReactNode;
}

export const SlidingDrawer = ({
  show,
  background,
  color,
  callback,
  children,
}: Props): JSX.Element => {
  return (
    <div>
      <div>
        <div
          style={{ background: background, color: color }}
          className={show ? "drawer-open" : "drawer-close"}
        >
          <button
            onClick={():void => {
              callback();
            }}
          >
            make the slider close
          </button>
          <p>SNAKES</p>
          {children}
        </div>
      </div>
      <div className={show ? "backdrop-show" : "backdrop-hide"}></div>
    </div>
  );
};
